// react & redux
import React from 'react'

// core classes & libs
import ChartWellSelectable from './ChartWellSelectable'
import autobind from 'autobind-decorator'
import config from './configs/SummaryEURbyDrillType.config'
import API from '../../lib/api-store'

@autobind class SummaryEURbyDrillType extends ChartWellSelectable {
  constructor(props) {
    console.log('>>>>>>>>>>>>>>>INSIDE CONSTRUCTOR')
    super(props)

    super.loadConfig(config)

    // Need to zoom and select...
    // super.loadConfig(config, {
    //  'xAxis.events.setExtremes': function(e) {
    //    this.setExtremes(e.min, e.max)
    //  }})
  }

  loadData(nextProps) {
    let component = this
    let props = nextProps || this.props
    let { oilGasLookUp } = props
    let structToPlot = oilGasLookUp[props.state.productionType + props.state.reservesType]
    this.setState({ isLoading: true })

    API.get('wells?options=utcdates', (err, wells) => {
      if (err || !this._isMounted) return false

      // transform data from API
      let seriesData = { Vertical: [], Horizontal: [], Deviated: [], Unknown: [] }
      let monthNames = ["Jan", "Feb", "Mar", "Apr", "May", "Jun",
        "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"
      ];

      Object.keys(wells).forEach(wUID => {
        let well = wells[wUID]
        let spudDate = new Date(well.spud_date)
        let dataPoints = {
          x: well.spud_date,
          y: well[structToPlot.dataToPlot] / structToPlot.conversionFactor,
          wUID: well.wUID,
          name: well.well_name,
          yUnitLabel: structToPlot.yLabel,
          units: structToPlot.units,
          spudDate: monthNames[spudDate.getMonth()] + ' ' + spudDate.getFullYear()
        }

        if (well.drill_type === 'V') {
          seriesData.Vertical.push(dataPoints)
        } else if (well.drill_type === 'H') {
          seriesData.Horizontal.push(dataPoints)
        } else if (well.drill_type === 'D') {
          seriesData.Deviated.push(dataPoints)
        } else {
          seriesData.Unknown.push(dataPoints)
        }
      })

      // inject transformed data into state.config
      component.setState({
        isLoading: false,
        config: component.state.config.update('series', all => all.map(
          series => series.set('data', seriesData[series.get('name')])
        ))
        .setIn(['title', 'text'], structToPlot.yLabel + ' by Drill Type')
        .setIn(['yAxis', 'title', 'text'], structToPlot.yLabel + ' [' + structToPlot.units + ']')
      })

      // this calls a redraw, so none needed
      component.selectWellsFromState(props.state.wellSelection)
    })
  }

  shouldComponentUpdate(nextProps, nextState) {
    return super.shouldComponentUpdate(nextProps, nextState) ||
      (nextProps.state.productionType !== this.props.state.productionType) ||
      (nextProps.state.reservesType !== this.props.state.reservesType)
  }

  componentWillReceiveProps(nextProps) {
    super.componentWillReceiveProps(nextProps)
    if (nextProps.state.productionType !== this.props.state.productionType ||
        nextProps.state.reservesType !== this.props.state.reservesType) {
      this.loadData(nextProps)
    }
  }
}

export default SummaryEURbyDrillType



