// react & redux
import React from 'react'
import API from '../../lib/api-store'

// child components
import OperatorItem from './OperatorItem'

export const OperatorSelectionPanel = React.createClass({
  getInitialState: function() {
    return {
      isLoading: false,
      operators: []
    }
  },

  componentWillMount() {
    this.selection = new Set(this.props.state.operatorSelection)
    this.loadData()
  },

  // subsequent renders loads from new props
  componentWillReceiveProps: function(nextProps) {
    let s2 = nextProps.state
    let s1 = this.props.state

    if (nextProps.portfolio !== this.props.portfolio) {
      this.selection.clear()
      this.loadData()
    }
  },

  toggleOperator(name) {
    let selection = this.selection;
    let { operatorSelection } = this.props.actions
    let { operators } = this.state
    let operator = operators.find(o => o.name === name)

    operator.selected = !operator.selected
    if (operator.selected) {
      operatorSelection.addOperator(name)
    } else {
      operatorSelection.removeOperator(name)
    }

    this.setState({})
  },

  selectAll() {
    let { operatorSelection } = this.props.actions
    let { operators } = this.state

    operators.forEach(o => o.selected = true)
    this.setState({})
    operatorSelection.setOperators(operators.map(o => o.name.replace(/,/g,'')))
  },

  selectNone() {
    let { operatorSelection } = this.props.actions
    let { operators } = this.state

    operators.forEach(o => o.selected = false)
    this.setState({})
    operatorSelection.clearOperators()
  },

  loadData() {
    let operatorActions = this.props.actions.operatorSelection
    let { operatorSelection } = this.props.state

    this.setState({ isLoading: true })

    API.get('operators').then(operators => {
      let operatorList = operators.map(o => o.name.replace(/,/g,''))

      if (!this.selection.size) {
        this.selection = new Set(operatorList)
        operatorActions.setOperators(operatorList) // dispatch to state
      }

      this.setState({ isLoading: false, operators: operators.map(o => ({ name: o.name.replace(/,/g,''), color: o.color, wells: o.wells, selected: this.selection.has(o.name.replace(/,/g,'')) })) })
    })
  },

  render() {
    let { operators } = this.state

    const operatorOptions = operators.map(operator =>
      <OperatorItem
        key={operator.name}
        operator={operator}
        handleClick={this.toggleOperator}
        />
    )

    let numSelected = operators.map(o => o.selected ? 1 : 0).reduce((a, b) => a + b, 0)

    console.log('OperatorSelectionPanel:rendering...')
    return (
      <div className="side-panel operator-selection">
        <div className="handle">
          <span className="title"><small>{numSelected}</small> Operators</span>
        </div>
        <div className={'content' + (this.state.isLoading ? ' isLoading' : '')}>
          <div className="selection-control">
            <a className={numSelected === operators.length ? 'disabled' : ''} onClick={this.selectAll}>All</a>
            <a className={numSelected === 0 ? 'disabled' : ''} onClick={this.selectNone}>None</a>
          </div>
          <div className="items">
            {operatorOptions}
          </div>
        </div>
      </div>
    )
  }
})

export default OperatorSelectionPanel



