import ReduxRegistry from 'redux-registry'
import { List, Map, fromJS } from 'immutable'

let register = new ReduxRegistry()

register
  .setInitialState(fromJS({
    selected: undefined,
    isFetching: false,
    available: []
  }))
  .add({
    alias: 'select',
    create: portfolio => ({
      type: 'SELECT_PORTFOLIO',
      portfolio
    }),
    reduce: (state, action) => state.set('selected', action.portfolio)
  })
  .add({
    alias: 'clear',
    create: () => ({
      type: 'CLEAR_PORTFOLIO'
    }),
    reduce: (state, action) => state.set('selected', undefined)
  })
  .add({
    alias: 'fetchAvailable',
    create: () => ({
      type: 'FETCH_AVAILABLE_PORTFOLIOS'
    }),
    reduce: state => state.set('isFetching', true)
  })
  .add({
    alias: 'receiveAvailable',
    create: (portfolios) => ({
      type: 'RECEIVE_AVAILABLE_PORTFOLIOS',
      portfolios
    }),
    reduce: (state, action) => state
                      .set('isFetching', false)
                      .set('available', action.portfolios)
  })
;

export default register



