// react & redux
import React from 'react'
import { connect } from 'react-redux'
import API from '../../lib/api-store'

// reducers & actions
import { automapState, automapActions } from '../../redux/helpers'

// child components
import WellItem from './Wellitem'

export const WellList = React.createClass({
  getInitialState: function() {
    return {
      isLoading: false,
      wells: []
    }
  },

  componentWillMount() {
    console.log('componentDidMount', this.props)
    let { portfolio } = this.props
    this.selection = new Set();
    portfolio && this.loadData()
  },

  // subsequent renders loads from new props
  componentWillReceiveProps: function(nextProps) {
    let s2 = nextProps.state
    let s1 = this.props.state

    if (nextProps.portfolio !== this.props.portfolio) {
      this.loadData()
    }
  },

  toggleWell(label) {
    let selection = this.selection;
    let { wellSelection } = this.props.actions

    if (selection.has(label)) {
      this.selection = selection.delete(label)
      wellSelection.removeOperator(label) // dispatch to state
    } else {
      this.selection = selection.add(label)
      // wellSelection.addOperator(label) // dispatch to state
    }
  },

  loadData() {
    let { wellSelection } = this.props.actions

    this.setState({ isLoading: true })
    API.get('wells?options=utcdates').then(data => {
      let wells = Object.keys(data).map(wUID => data[wUID])
      this.setState({ isLoading: false, wells })
      this.selection = new Set(wells)
      // wellSelection.setOperators(wells) // dispatch to state
    })
  },

  render() {
    let { wells } = this.state
    let { wellSelection } = this.props.state
    let { actions } = this.props

    window.wellSelection = wellSelection

    const wellItems = wells.map(well =>
      <WellItem key={well.wUID} well={well} selected={wellSelection.includes(well.wUID)} actions={actions.wellSelection} />
    )

    console.log('WellList:rendering...')

    return (
      <div className={'side-panel well-list' + (this.state.isLoading ? ' isLoading' : '')}>
        <div className="handle">
          <span className="title"><small>{ wells.length }</small> Wells</span>
        </div>
        <div className="items">
          {wellItems}
        </div>
      </div>
    )
  }
})

export default WellList

export const WellListContainer = connect(
  automapState('wellSelection', 'portfolio'),
  automapActions('wellSelection', 'portfolio')
)(WellList)



