// react & redux
import React from 'react'

// child components
import SpudDateMap from '../Maps/SpudDateMap'
import ReservesDecline from '../Charts/ReservesDecline'
import ReservesBoxWhisker from '../Charts/ReservesBoxWhisker'
import EntitySelectionPanel from '../Panels/EntitySelectionPanel'

const Reserves = ({ state, actions, portfolio }) => {
  let props = { state, actions, portfolio }

  let oilGasRateLookUp = {
    'OIL': {
      dataToPlot: 'oil',
      yLabel: 'Oil Rate',
      units: 'STB/D'
    },
    'GAS': {
      dataToPlot: 'gas',
      yLabel: 'Gas Rate',
      units: 'MSCF/D'
    }
  }

  let oilGasReservesLookUp = {
    'OILEUR': {
      dataToPlot: 'oil_eur',
      yLabel: 'Oil EUR',
      units: 'MSTB',
      conversionFactor: 1e3
    },
    'GASEUR': {
      dataToPlot: 'gas_eur',
      yLabel: 'Gas EUR',
      units: 'MMSCF',
      conversionFactor: 1e3
    },
    'OILPDP': {
      dataToPlot: 'oil_pdp',
      yLabel: 'Oil PDP',
      units: 'MSTB',
      conversionFactor: 1e3
    },
    'GASPDP': {
      dataToPlot: 'gas_pdp',
      yLabel: 'Gas PDP',
      units: 'MMSCF',
      conversionFactor: 1e3
    }
  }

  return (
    <div className="page reserves-page">
      <EntitySelectionPanel {...props} />
      <div className="panels">
        <ReservesDecline {...props} oilGasLookUp={oilGasRateLookUp} />
        <ReservesBoxWhisker {...props} oilGasLookUp={oilGasReservesLookUp} />
        <SpudDateMap {...props} />
      </div>
    </div>
  )
}

export default Reserves



