// react & redux
import React from 'react'
import ReactHighcharts from 'react-highcharts'

// core classes & libs
import Highcharts from 'highcharts'
import Expander from '../Expander'

export const Chart = React.createClass({
  getInitialState() {
    return {
      isExpanded: false
    }
  },

  toggleExpanded() {
    this.setState({ isExpanded: !this.state.isExpanded })

    setTimeout(() => { this.chart.reflow() }, 10)
  },

  componentWillUnmount() {
    // if (this.chart) {
    //  this.chart.destroy()
    // }
  },

  chartLoaded(chart) {
    this.chart = chart

    this.props.callback && this.props.callback(chart)
  },

  render() {
    let { isLoading, config, callback } = this.props
    let { isExpanded } = this.state

    return (
      <div className={'panel chart' + (isLoading ? ' isLoading' : '') + (isExpanded ? ' expanded' : '')}>
        <Expander toggleHandler={this.toggleExpanded} expanded={isExpanded} />
        <ReactHighcharts isPureConfig={true} config={config} callback={this.chartLoaded} />
      </div>
    )
  }
})

export default Chart



