// react & redux
import React from 'react'
import ReactHighcharts from 'react-highcharts'

// core classes & libs
import Highcharts from 'highcharts'
import Expander from './Expander'

export const Image = React.createClass({
  getInitialState() {
    return {
      isExpanded: false
    }
  },

  toggleExpanded() {
    this.setState({ isExpanded: !this.state.isExpanded })
  },

  render() {
    let { portfolio, type, extension } = this.props
    let { isExpanded } = this.state

    return (
      <div className={'panel image' + (isExpanded ? ' expanded' : '')}>
        <Expander toggleHandler={this.toggleExpanded} expanded={isExpanded} />
        <img src={`/images/by-portfolio/${portfolio}.${type}.${extension}`} width="100%" height="100%" />
      </div>
    )
  }
})

export default Image



