// react & redux
import React from 'react'

// child components
import HistoryProductionBar from '../Charts/HistoryProductionBar'
import HistoryEURbySpud from '../Charts/HistoryEURbySpud'
import HistoryGroupedPie from '../Charts/HistoryGroupedPie'
import EntitySelectionPanel from '../Panels/EntitySelectionPanel'

const History = ({ state, actions, portfolio }) => {
  let props = { state, actions, portfolio }

  let oilGasRateLookUp = {
    'OIL': {
      dataToPlot: 'oil',
      yLabel: 'Oil Rate',
      units: 'STB/D'
    },
    'GAS': {
      dataToPlot: 'gas',
      yLabel: 'Gas Rate',
      units: 'MSCF/D'
    }
  }

  let oilGasReservesLookUp = {
    'OILEUR': {
      dataToPlot: 'oil_eur',
      yLabel: 'Oil EUR',
      units: 'MSTB',
      conversionFactor: 1e3
    },
    'GASEUR': {
      dataToPlot: 'gas_eur',
      yLabel: 'Gas EUR',
      units: 'MMSCF',
      conversionFactor: 1e3
    },
    'OILPDP': {
      dataToPlot: 'oil_pdp',
      yLabel: 'Oil PDP',
      units: 'MSTB',
      conversionFactor: 1e3
    },
    'GASPDP': {
      dataToPlot: 'gas_pdp',
      yLabel: 'Gas PDP',
      units: 'MMSCF',
      conversionFactor: 1e3
    }
  }

  return (
    <div className="page history-page">
      <EntitySelectionPanel {...props} />
      <div className="panels">
        <HistoryProductionBar {...props} oilGasLookUp={oilGasRateLookUp} />
        <HistoryEURbySpud {...props} oilGasLookUp={oilGasReservesLookUp} />
        {/*<HistoryGroupedPie portfolio={portfolio} yValue = "oil_eur" yPercent = "" units = "MSTB" chartTitle = "Oil EUR by Spud Date" />*/}
      </div>
    </div>
  )
}

export default History



