import ReduxRegistry from 'redux-registry'

let register = new ReduxRegistry()

register
  .setInitialState('OIL')
  .add({
    alias: 'toggle',
    create: () => ({
      type: 'TOGGLE_PRODUCTION_TYPE'
    }),
    reduce: function(state, action) {
      return state === 'OIL' ? 'GAS' : 'OIL'
    }
  })

export default register



