// react & redux
import React from 'react'

// core classes & libs
import ChartEntityFilterable from './ChartEntityFilterable'
import autobind from 'autobind-decorator'
import config from './configs/ReservesBoxWhisker.config'
import async from 'async'
import API from '../../lib/api-store'

@autobind class ReservesBoxWhisker extends ChartEntityFilterable {
  constructor(props) {
    super(props)

    super.loadConfig(config)
  }

  loadData(entities, nextProps) {
    let component = this
    let props = nextProps || this.props
    let groups = this.getSeriesList(entities)
    let { oilGasLookUp } = props
    let structToPlot = oilGasLookUp[props.state.productionType + props.state.reservesType]
    this.setState({ isLoading: true })

    async.parallel({
      production: cb => this.getProductionData(groups, (err, data) => cb(null, data)),
      wells: cb => API.get('wells?options=utcdates').then(data => cb(null, data))
    }, (err, data) => {
      if (err || !component._isMounted) return false

      let seriesData = []
      let { wells, production } = data

      groups.forEach((group, groupIndex) => {
        let series = { data: [], name: group.name, color: group.color }

        production[group.name].wells.forEach((wUID, index) => {
          let well = wells[wUID]

          series.data.push({
            x: Math.random() * ((groupIndex+0.1) - (groupIndex-0.1)) + (groupIndex-0.1),
            y: well[structToPlot.dataToPlot] / structToPlot.conversionFactor,
            yUnitLabel: structToPlot.yLabel,
            units: structToPlot.units,
            name: well.well_name,
            seriesName: group.name
          })
        })

        seriesData.push(series)
      })

      component.setState({
        isLoading: false,
        config: component.state.config.set('series', seriesData)
        .setIn(['yAxis', 'title', 'text'], structToPlot.yLabel + ' [' + structToPlot.units + ']')
      })
    })
  }

  shouldComponentUpdate(nextProps, nextState) {
    return super.shouldComponentUpdate(nextProps, nextState) ||
      (nextProps.state.productionType !== this.props.state.productionType) ||
      (nextProps.state.reservesType !== this.props.state.reservesType)
  }

  componentWillReceiveProps(nextProps) {
    super.componentWillReceiveProps(nextProps)
    if (nextProps.state.productionType !== this.props.state.productionType ||
        nextProps.state.reservesType !== this.props.state.reservesType) {
      this.loadData(this.props.state.hierarchy.get('entities'), nextProps)
    }
  }
}

export default ReservesBoxWhisker




