// react & redux
import React from 'react'

// child components
import PortfolioProductionHistory from '../Charts/PortfolioProductionHistory'
import PortfolioDrillHistory from '../Charts/PortfolioDrillHistory'
import SummaryEURbyDrillType from '../Charts/SummaryEURbyDrillType'
import { WellListContainer } from '../Panels/WellList'
import DrillTypeMap from '../Maps/DrillTypeMap'

const Summary = ({ state, actions, portfolio }) => {
  let props = { state, actions, portfolio }

  let oilGasLookUp = {
    'OILEUR': {
      dataToPlot: 'oil_eur',
      yLabel: 'Oil EUR',
      units: 'MSTB',
      conversionFactor: 1e3
    },
    'GASEUR': {
      dataToPlot: 'gas_eur',
      yLabel: 'Gas EUR',
      units: 'MMSCF',
      conversionFactor: 1e3
    },
    'OILPDP': {
      dataToPlot: 'oil_pdp',
      yLabel: 'Oil PDP',
      units: 'MSTB',
      conversionFactor: 1e3
    },
    'GASPDP': {
      dataToPlot: 'gas_pdp',
      yLabel: 'Gas PDP',
      units: 'MMSCF',
      conversionFactor: 1e3
    }
  }

  return (
    <div className="page summary-page">
      <WellListContainer portfolio={portfolio} />
      <div className="panels">
        <PortfolioProductionHistory {...props} />
        <PortfolioDrillHistory {...props} />
        <SummaryEURbyDrillType {...props} oilGasLookUp={oilGasLookUp} />
        <DrillTypeMap {...props} />
      </div>
    </div>
  )
}

export default Summary




