import ReduxRegistry from 'redux-registry'

let register = new ReduxRegistry()

register
  .setInitialState('SELECT')
  .add({
    alias: 'toggle',
    create: () => ({
      type: 'TOGGLE_SELECTION_TYPE'
    }),
    reduce: (state, action) => state === 'SELECT' ? 'ZOOM' : 'SELECT'
  })
;

export default register



