// react & redux
import React from 'react'
import autobind from 'autobind-decorator'
import { fromJS } from 'immutable'

// child components
import Select from 'react-select'
import 'react-select/dist/react-select.css'
import EntityNode from './EntityNode'

const entities = ['field', 'reservoir', 'formation', 'operator'].map(type => ({ value: type, label: type }))

@autobind class EntitySelector extends React.Component {
  constructor(props) {
    super(props)
  }

  getEntitiesFromList(entities, ids) {
    return entities.filter(entity => ids.indexOf(entity.id) !== -1).sort((a, b) => a.name < b.name ? -1 : 1)
  }

  filterEntities(entities, hierarchy) {
    if (!entities || !hierarchy.length || !Object.keys(entities).length) return []
    let parentNode = entities[hierarchy[0]+'s']
    let childrenNode = entities[hierarchy[1]+'s']
    let childType = hierarchy.length > 1 ? hierarchy[1] : undefined
    let parents = fromJS(entities[hierarchy[0]+'s'].sort((a, b) => a.name < b.name ? -1 : 1)).toJS()

    if (childType) {
      parents.forEach(parent => {
        parent.children = this.getEntitiesFromList(childrenNode, parent[childType + 's'])
      })
    }

    return parents
  }

  shouldComponentUpdate(props, state) {
    this.setState({})
    return true
  }

  isSelected(e, parent) {
    return this.props.selection.find(s => {
      let rootMatch = s.id === e.id && s.type === e.type
      let parentMatch =
        !parent || !s.parent ||
        (parent.id === s.parent.id && parent.type === s.parent.type)

      // console.log('comparing parents', parent, s.parent, parentMatch)
      return rootMatch && parentMatch
    })
  }

  toggleEntity(e, parent) {
    let { selection, addHandler, removeHandler } = this.props
    let { type, id, name, color } = e

    if (this.isSelected(e, parent)) {
      removeHandler({ id, type })
    } else {
      addHandler({ id, name, type, color }, parent)
    }
  }

  render() {
    // const { value, disabled } = this.state
    const { hierarchy, entities, colors, isLoading } = this.props

    console.log('ENTITY SELECTOR:render')
    return (
      <div className={'entity-selector items' + (isLoading ? ' isLoading' : '')}>
        {
          this.filterEntities(entities, hierarchy).map(item =>
            <EntityNode
              key={item.id}
              item={item}
              toggleHandler={this.toggleEntity}
              isSelected={this.isSelected}
              />
          )
        }
      </div>
    )
  }
}

EntitySelector.propTypes = {
  entities: React.PropTypes.object,
  colors: React.PropTypes.object,
  hierarchy: React.PropTypes.array,
  isLoading: React.PropTypes.bool
}

export default EntitySelector



