// react & redux
import React from 'react'
import { render } from 'react-dom'
import API from '../../lib/api-store'
import autobind from 'autobind-decorator'

// core libs etc
import async from 'async'

// leaflet specifics
import L from 'leaflet'
import BingLayer from 'leaflet-bing-layer'
import 'leaflet/dist/leaflet.css'

let config = {
  params: {
    center: [40.655769,-73.938503],
    zoomControl: false,
    zoom: 9,
    maxZoom: 19,
    minZoom: 7,
    scrollwheel: true,
    legends: true,
    infoControl: false,
    attributionControl: true
  },
  tileLayer: {
    bingMapsKey: 'AmW5OZZX5GeD-CITRJ8QulcVjbzeW4AmImnDa_obhKMHRX4HM7gYVcz9_4oyB4hM',
    imagerySet: 'Road'
  }
}

// DEFINE CLASS
@autobind class LeafletMap extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      isLoading: false,
      markers: [],
    }
  }

  // componentDillMount() {
  //  this.loadData()
  // }

  componentDidMount() {
    console.log('MAP:componentDidMount')
    // code to run just after the component "mounts" / DOM elements are created
    // we could make an AJAX request for the GeoJSON data here if it wasn't stored locally
    // create the Leaflet map object
    this._isMounted = true
    this.loadData()

    if (!this.state.map) {
      this.init(this._mapNode)
    }
  }

  componentWillUnmount() {
    this._isMounted = false
  }

  componentDidUpdate(prevProps, prevState) {
    console.log('MAP:componentDidUpdate')
    // code to run when the component receives new props or state
    // check to see if geojson is stored, map is created, and geojson overlay needs to be added
    if (this.state.geojson && this.state.map && !this.state.geojsonLayer) {
      // add the geojson overlay
      this.addGeoJSONLayer(this.state.geojson)
    }

    if (this.state.map && this.state.geojsonLayer) {
      this.filterGeoJSONLayer()
    }
  }

  geoJSONPath() {
    console.log('MAP:geoJSONPath')
    let pUID = this.props.state.portfolio.get('selected')

    let portfolios = this.props.state.user.get('portfolios').toJS()
    let portfolio = portfolios.find(p => p.pUID === pUID)

    // console.log('pUID & portfolios & portfolio & layer', pUID, portfolios, portfolio, portfolio && portfolio.layer)
    if (portfolio && portfolio.layer) {
      return `/geoserver?typeNames=${portfolio.layer}&srsName=${portfolio.projection}`
    }

    return false
  }

  // API CALL ADDS WELL LOCATION LAYER AND APPLIES STYLING TO MAP
  loadData() {
    console.log('MAP:loadData')
    let component = this

    this.setState({ isLoading: true })

    async.parallel({
      geo: API.async('/geoserver?typeNames=spa:janus&srsName=EPSG:4267'),
      wells: API.async('wells'),
    }, (err, data) => {
      console.log('**** MAP: loadData() complete')
      this.setState({
        isLoading: false,
        wells: data.wells,
        geojson: data.geo
      })
    })
  }

  addGeoJSONLayer(geojson) {
    console.log('MAP:addGeoJSONLayer')
    // create a native Leaflet GeoJSON SVG Layer to add as an interactive overlay to the map
    // an options object is passed to define functions for customizing the layer
    const geojsonLayer = L.geoJson(geojson, {
      onEachFeature: this.onEachFeature,
      pointToLayer: this.pointToLayer,
      filter: this.filterFeatures
    })
    // add our GeoJSON layer to the Leaflet map object
    geojsonLayer.addTo(this.state.map)
    // store the Leaflet GeoJSON layer in our component state for use later
    this.setState({ geojsonLayer })
    // fit the geographic extent of the GeoJSON layer within the map's bounds / viewport
    this.zoomToFeature(geojsonLayer)
  }

  filterGeoJSONLayer() {
    console.log('MAP:filterGeoJSONLayer')
    // clear the geojson layer of its data
    this.state.geojsonLayer.clearLayers()

    // re-add the geojson before filtering
    this.state.geojsonLayer.addData(this.state.geojson)

    // fit the map to the new geojson layer's geographic extent
    this.zoomToFeature(this.state.geojsonLayer)
  }

  zoomToFeature(target) {
    console.log('MAP:zoomToFeature', target.getBounds())
    // pad fitBounds() so features aren't hidden under the Filter UI element
    var fitBoundsParams = {
      paddingTopLeft: [10,10],
      paddingBottomRight: [10,10]
    }
    // set the map's center & zoom so that it fits the geographic extent of the layer
    this.state.map.fitBounds(target.getBounds(), fitBoundsParams)
  }

  filterFeatures(feature, layer) {
    return true;
  }

  colorIndex() {
    return {
      'SHUT_IN': '#666',
      'INACTIVE': '#aaa'
    }
  }

  colorBy(well) {
    return this.colorIndex[well.status]
  }

  createColors() {
    return {
      'SHUT_IN': '#666',
      'INACTIVE': '#aaa'
    }
  }

  colorBy(well) {
    this.colors = this.colors || this.createColors()

    return this.colors[well.status]
  }

  pointToLayer(feature, latlng) {
    // renders our GeoJSON points as circle markers, rather than Leaflet's default image markers
    // parameters to style the GeoJSON markers

    let { wells } = this.state
    let well = wells[feature.properties.wUID]

    var markerParams = {
      radius: 6,
      fillColor: this.colorBy(well) || 'orange',
      color: '#fff',
      weight: 1,
      opacity: 0.7,
      fillOpacity: 0.9
    }

    let point = L.circleMarker(latlng, markerParams)

    return point
  }

  popupAttributes() {
    return [
      'status',
      'field',
      'formation',
      'reservoir',
      'drill_type',
      'spud_date'
    ]
  }

  onEachFeature(feature, layer) {
    let { wells } = this.state
    let well = wells[feature.properties.wUID]

    let display = this.popupAttributes()

    // console.log('calling onEachFeature', well, layer);
    // if (feature.properties) {
      // assemble the HTML for the markers' popups (Leaflet's bindPopup method doesn't accept React JSX)
      const popupContent = `<h3>${well.well_name}</h3><table><tbody>` +
        display.map(attr => `<tr><th>${attr.replace(/\_/g,' ')}</th><td>${well[attr]}</td></tr>`).join('') +
        '</tbody></table>'

      // add our popups
      layer.bindPopup(popupContent)
    // }
  }

  init(id) {
    console.log('MAP:init')
    if (this.state.map) return

    // this function creates the Leaflet map object and is called after the Map component mounts
    let map = L.map(id, config.params)
    L.control.zoom({ position: "bottomleft"}).addTo(map)
    L.control.scale({ position: "bottomleft"}).addTo(map)

    // a TileLayer is used as the "basemap"
    // const tileLayer = L.tileLayer(config.tileLayer.uri, config.tileLayer.params).addTo(map)
    const tileLayer = L.tileLayer.bing(config.tileLayer).addTo(map)

    // set our state to include the tile layer
    this.setState({ map, tileLayer })
  }

  render() {
    let { isLoading, markers } = this.state

    console.log('MAP:render')
    return (
      <div className="panel map">
        <div ref={(node) => this._mapNode = node} id="map" />
      </div>
    )
  }
}

export default LeafletMap



