// react & redux
import React from 'react'
import API from '../../lib/api-store'
import autobind from 'autobind-decorator'

// core libs etc
import async from 'async'

// leaflet specifics
import L from 'leaflet'
import BingLayer from 'leaflet-bing-layer'
import LeafletMap from './LeafletMap'

// DEFINE CLASS
@autobind class SpudDateMap extends LeafletMap {
  constructor(props) {
    super(props)
  }

  // API CALL ADDS WELL LOCATION LAYER AND APPLIES STYLING TO MAP
  loadData() {
    console.log('****MAP:loadData')
    let component = this

    let geoJSONpath = this.geoJSONPath()
    if (!geoJSONpath) return false

    this.setState({ isLoading: true })

    async.parallel({
      geo: callback => API.get(geoJSONpath).then(data => callback(null, data)),
      wells: callback => API.get('wells').then(data => callback(null, data))
    }, (err, data) => {
      if (err || !component._isMounted) return false

      console.log('data > ', data)
      this.setState({
        isLoading: false,
        wells: data.wells,
        geojson: data.geo
      })
    })
  }

  popupAttributes() {
    return [
      'status',
      'operator',
      'field',
      'formation',
      'reservoir',
      'drill_type',
      'spud_date'
    ]
  }

  findDateRange() {
    let { wells } = this.state
    let wellList = Object.keys(wells)
    let range = { min: Infinity, max: -Infinity, delta: undefined }

    wellList.forEach(wUID => {
      let well = wells[wUID]
      let year = (new Date(well.spud_date)).getFullYear()

      range.min = Math.min(range.min, year)
      range.max = Math.max(range.max, year)
    })

    range.delta = range.max - range.min

    console.log('range determined', range.min, 'to', range.max)

    return range
  }

  toHex(number) {
    return ('00'+number.toString(16)).slice(-2)
  }

  createColors() {
    let range = this.findDateRange()
    let colorIndex = {}
    let { toHex } = this
    let colorBase = {
      red: { min: 0, max: 50 },
      green: { min: 0, max: 150 },
      blue: { min: 0, max: 0 },
    }

    Object.keys(colorBase).forEach(type => {
      let color = colorBase[type]
      color.delta = color.max - color.min
    })

    for (let year = range.min; year<=range.max; year++) {
      let yearPercentage = (year - range.min) / range.delta

      let red = Math.round(yearPercentage * colorBase.red.delta + colorBase.red.min)
      let green = Math.round(yearPercentage * colorBase.green.delta + colorBase.green.min)
      let blue = Math.round(yearPercentage * colorBase.blue.delta + colorBase.blue.min)

      colorIndex[year] = '#' + toHex(red) + toHex(green) + toHex(blue)
    }

    return colorIndex
  }

  colorBy(well) {
    this.colors = this.colors || this.createColors()
    let year = (new Date(well.spud_date)).getFullYear()

    return this.colors[year]
  }
}

export default SpudDateMap



