// react & redux
import React from 'react'
import autobind from 'autobind-decorator'

@autobind class EntityNode extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      expanded: false,
      active: false,
      children: props.item.children || [],
    }
  }

  toggle() {
    let { item, parent } = this.props
    let slimParent = parent ? { id: parent.id, type: parent.type, name: parent.name } : undefined

    if (!item.children || !item.children.length) {
      // don't toggle parent nodes
      this.props.toggleHandler(this.props.item, slimParent)
    }
  }

  render() {
    const { expanded } = this.state
    const { item, toggleHandler, isSelected, parent } = this.props
    let className = 'item';

    if (item.children && item.children.length) {
      className += ' expandable'
    }

    if (expanded) {
      className += ' expanded'
    }

    if (isSelected(item, parent)) {
      className += ' active'
    }

    const childNodes = item.children && item.children.map(child =>
            <EntityNode
              item={child}
              key={child.id}
              parent={item}
              toggleHandler={toggleHandler}
              isSelected={isSelected}
              />
          )

    const childrenBlock = childNodes ? (
        <div className="children">
          {childNodes}
        </div>
      ) : ''

    const swatch = item.children && item.children.length
      ? ''
      : <span className="swatch" style={{'backgroundColor':item.color}}></span>

    return (
      <div className={className} onClick={this.toggle} >
        {swatch}
        {item.name}
        { childrenBlock }
      </div>
    )
  }
}

// EntityNode.propTypes = {
//    entities: React.PropTypes.object,
//    colors: React.PropTypes.object,
//    hierarchy: React.PropTypes.array,
//    isLoading: React.PropTypes.bool
// }

export default EntityNode



