import ReduxRegistry from 'redux-registry'

let register = new ReduxRegistry()

register
  .setInitialState(true)
  .add({
    alias: 'toggle',
    create: function() {
      return {
        type: 'TOGGLE_SIDEBAR'
      };
    },
    reduce: state => !state
  })

export default register



