import ReduxRegistry from 'redux-registry'
import { List, Map, fromJS } from 'immutable'

let register = new ReduxRegistry()

register
  .setInitialState(fromJS({
    types: List(['field']),
    entities: List()
  }))
  .add({
    alias: 'clear',
    create: () => ({
      type: 'CLEAR_HIERARCHY_ENTITES'
    }),
    reduce: state => state.set('entities', List())
  })
  .add({
    alias: 'setTypes',
    create: (values) => ({
      type: 'SET_HIERARCHY_TYPES',
      values
    }),
    reduce: (state, action) => {
      let newState = state
                      .set('types', List(action.values))
                      .set('entities', List())
      return newState
    }
  })
  .add({
    alias: 'addEntity',
    create: (entity, parent) => ({
      type: 'ADD_HIERARCHY_ENTITY',
      entity,
      parent
    }),
    reduce: (state, action) => {
      let { entity, parent } = action
      if (parent) {
        entity.parent = parent
        entity.name = parent.name + ' > ' + entity.name
        state = state.update('entities', all => all.filter(e => {
                  return !parent || (e.getIn(['parent','id']) === parent.id)
                }))
      }

      return state.update('entities', e => e.push(fromJS(entity)))
    }
  })
  .add({
    alias: 'removeEntity',
    create: (entity) => ({
      type: 'REMOVE_HIERARCHY_ENTITY',
      entity
    }),
    reduce: (state, action) => state.update('entities', e => e.filter(s => !(s.get('id') === action.entity.id && s.get('type') == action.entity.type)))
  })


export default register



