// react & redux
import React from 'react'

export const WellItem = ({ well, selected, actions }) => {
  function toggleWell() {
    actions.toggleWell(well.wUID)
  }

  return (
    <div className={'well-item' + (selected ? ' active' : '')} onClick={toggleWell} >
      {well.well_name}
    </div>
  )
}

export default WellItem



