// react & redux
import React from 'react'

// core classes & libs
import autobind from 'autobind-decorator'
import classNames from 'classnames'

// child components
import TextPanel from './TextPanel'

@autobind class OverviewText extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      isLoading: false,
      text: ''
    }
  }

  // first render loads from state
  componentDidMount() {
    this.loadData()
  }

  // subsequent renders loads from new props
  componentWillReceiveProps(nextProps) {
    const s1 = this.props.state
    const s2 = nextProps.state

    if (nextProps.portfolio !== this.props.portfolio) {
      this.loadData()
    }
  }

  // only update component on portfolio change
  // shouldComponentUpdate(nextProps, nextState) {
  //   let s1 = this.state
  //   let s2 = nextState

  //   if (nextProps.portfolio !== this.props.portfolio) return true

  //   return false
  // }

  loadData() {
    this.setState({ isLoading: true })
    API.get('?options=utcdates').then(data => {
      console.log('OvweviewText:loadData', data.overview_text)
      this.setState({ isLoading: false, text: data.overview_text || ''})
    })
  }

  render() {
    let { text, isLoading } = this.state

    return (
      <TextPanel className={classNames('panel', 'text')} isLoading={isLoading} text={text} />
    )
  }
}

export default OverviewText



