// react & redux
import React from 'react'

// core classes & libs
import { fromJS } from 'immutable'
import { extend } from './configs/helpers'
import autobind from 'autobind-decorator'
import _ from 'lodash'

// child components
import Chart from './Chart'

@autobind class ChartLoader extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      isLoading: false,
      expanded: false,
    }

    this.wellSelection = new Set
  }

  loadConfig(base, ...configs) {
    let component = this;

    this.state.config = fromJS(extend(base, {
      'chart.events.redraw': function(e) {
        console.log('redrawing chart')
      },
      'chart.events.selection': function(e) {
        console.log('EVENT:chart.events.selection(e)', e)
        let eX = e.xAxis[0]
        let eY = e.yAxis[0]

        // reset and start adding to new selection
        component.resetWellSelection()
        component.wellSelection.clear()

        this.series.forEach(series => {
          series.points.forEach(p => {
            if (p.x >= eX.min && p.x <= eX.max && p.y >= eY.min && p.y <= eY.max) {
              p.select(true, true)
            }
          })
        })

        return false
      },
      'plotOptions.series.point.events.select': e => {
        let point = e.target
        point.graphic && point.graphic.toFront()
        component.addWellToSelection(point.wUID)
      },
      'plotOptions.series.point.events.unselect': e => {
        let point = e.target
        component.removeWellFromSelection(point.wUID)
      }
    }, ...configs))
  }


  addWellToSelection(well) {
    this.wellSelection.add(well)
    this.flushWellSelection()
  }

  removeWellFromSelection(well) {
    if (this.wellSelection.delete(well)) {
      this.flushWellSelection()
    }
  }

  sendWellSelectionToState() {
    let wellSelectionActions = this.props.actions.wellSelection
    let stateWellSelection = this.props.state.wellSelection

    let a = new Set(stateWellSelection.toJS())
    let b = this.wellSelection

    if (!_.isEqual(a, b)) {
      wellSelectionActions.setWells(this.wellSelection)
    }
  }

  flushWellSelection() {
    // clear previous timer
    clearTimeout(this.flushTimer)

    // set new timer
    this.flushTimer = setTimeout(this.sendWellSelectionToState, 2)
  }

  resetWellSelection() {
    this.chart.getSelectedPoints().forEach(point => point.select(false, false))
  }

  selectWellsFromState(wells) {
    let { series } = this.chart

    this.chart.getSelectedPoints().forEach(point => {
      if (!wells.has(point.wUID)) {
        point.select(false, false)
      }
    })

    wells.forEach(wUID =>
      series.forEach(points =>
        points.data.forEach(point => {
          if (point.wUID === wUID) {
            // only select unselected points
            if (!point.selected) {
              point.select(true, true)
            }
          }
        })
      )
    )
  }

  // first render loads from state
  componentDidMount() {
    this._isMounted = true
    this.loadData()
  }

  componentWillUnmount() {
    this._isMounted = false
  }

  // subsequent renders loads from new props
  componentWillReceiveProps(nextProps) {
    const s1 = this.props.state
    const s2 = nextProps.state
    const { chart } = this

    if (nextProps.portfolio !== this.props.portfolio) {
      this.loadData()
    }

    // receive state wellSelection and set internally
    if (s2.wellSelection !== s1.wellSelection) {
      this.selectWellsFromState(s2.wellSelection)
    }
  }

  // only update component on portfolio change
  shouldComponentUpdate(nextProps, nextState) {
    let s1 = this.state
    let s2 = nextState

    if (nextProps.portfolio !== this.props.portfolio) return true
    if (s1.config !== s2.config) return true
    if (s1.isLoading !== s2.isLoading) return true

    return false
  }

  chartLoaded(chart) {
    this.chart = chart
  }

  render() {
    let { isLoading, config } = this.state
    let { parent } = this.props

    return (
      <Chart parent={parent} isLoading={isLoading} config={config.toJS()} callback={this.chartLoaded} />
    )
  }
}

export default ChartLoader



