// react & redux
import React from 'react'
import { connect } from 'react-redux'
import { automapState, automapActions } from '../../redux/helpers'

// child components
import Productbar from './Productbar'
import Productspace from './Productspace'
import LoginForm from '../User/LoginForm'

const App = ({ children, state, actions }) => {
  let { user, app } = state
  let { logout, login } = actions.user

  let body = !!user
    ? <Productspace>
        {children}
      </Productspace>
    : <LoginForm loginAction={login} user={user} />

  return (
    <div className="spa-app">
      <Productbar app={app} user={user} logoutAction={logout} />
      { body }
    </div>
  )
}

export default App

export const AppContainer = connect(
  automapState('user', 'app'),
  automapActions('user', 'app')
)(App)



