// react & redux
import React from 'react'

// child components
import PortfolioVsAveragePortf from '../Charts/PortfolioVsAveragePortf'
import PortfolioVsAverageWell from '../Charts/PortfolioVsAverageWell'

const PortfolioVsAverage = ({ state, actions }) => {
  const portfolio = state.portfolio.get('selected');

  return (
    <div className="page portfolio-vs-average-page">
      <PortfolioVsAveragePortf state={state} actions={actions} portfolio={portfolio} phase="OPR" units="STB/D" seriesName="Total Portfolio Oil Rate" />
      <PortfolioVsAverageWell state={state} actions={actions} portfolio={portfolio} phase="wellAvgOPR" units="STB/D" seriesName="Average Well Oil Rate" />
      <PortfolioVsAveragePortf state={state} actions={actions} portfolio={portfolio} phase="GPR" units="MSCF/D" seriesName="Total Portfolio Gas Rate" />
      <PortfolioVsAverageWell state={state} actions={actions} portfolio={portfolio} phase="wellAvgGPR" units="MSCF/D" seriesName="Average Well Gas Rate" />
      <PortfolioVsAveragePortf state={state} actions={actions} portfolio={portfolio} phase="WPR" units="BBL/D" seriesName="Total Portfolio Water Rate" />
      <PortfolioVsAverageWell state={state} actions={actions} portfolio={portfolio} phase="wellAvgWPR" units="BBL/D" seriesName="Average Well Water Rate" />
    </div>
  )
}

export default PortfolioVsAverage



