// react & redux
import React from 'react'

// child components
import StateGroupItem from './StateGroupItem'


const StateGroup = ({ options, state, action, active }) => {
  if (active) {
    return (
      <div className="state-group">
        {
          options.map((option, index) =>
            <StateGroupItem key={index} text={option.label} active={state === option.value} action={action} />
          )
        }
      </div>
    )
  } else {
    return false
  }
}

export default StateGroup



