// react & redux
import React from 'react'
import autobind from 'autobind-decorator'

// child components
import Select from 'react-select'
import 'react-select/dist/react-select.css'

const entities = ['field', 'reservoir', 'formation', 'operator'].map(type => ({ value: type, label: type }))

@autobind class HierarchySelector extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      disabled: false,
      value: (props.selected || []).map(i => ({ value: i, label: i }))
    }
  }

  arrow() {}

  select(value) {
    this.props.selectHandler(value.map(v => v.value))
    this.setState({ value, disabled: value.length === 2 })
  }

  filterOption(option, str) {
    return this.state.value.length < 2 && option.label.indexOf(str) !== -1
  }

  render() {
    let { value, disabled } = this.state

    return (
      <div className={'hierarchy-selector'}>
        <Select
          multi={true}
          autosize={true}
          options={entities}
          filterOption={this.filterOption}
          value={value}
          clearable={false}
          placeholder="Select a Hierarchy"
          onChange={this.select}
          arrowRenderer={this.arrow}
          />
      </div>
    )
  }
}

HierarchySelector.propTypes = {
  selectHandler: React.PropTypes.func.isRequired,
  selected: React.PropTypes.array,
}

export default HierarchySelector



