// react & redux
import React from 'react'
import { connect } from 'react-redux'

// core classes & libs
import { Map, fromJS, List } from 'immutable'

// reducers & actions
import { automapState, automapActions } from '../../../redux/helpers'

// child components
import { WorkspaceToolbarContainer } from './Toolbar'
import WorkspaceMessage from './WorkspaceMessage'

const Workspace = ({ children, state, actions }) => {
  let { portfolio } = state
  let body  = !portfolio.get('selected')
            ? <WorkspaceMessage message="Please select a portfolio before continuing..." />
            : React.Children.map(children, function(child) {
                return React.cloneElement(child, { state, actions, portfolio: state.portfolio.get('selected') });
              })

  return (
    <div className="workspace">
      <WorkspaceToolbarContainer />
      { body }
    </div>
  )
}

export default Workspace

export const WorkspaceContainer = connect(
  automapState('portfolio', 'chartOptions', 'wellSelection', 'operatorSelection', 'productionType', 'reservesType', 'hierarchy', 'user'),
  automapActions('portfolio', 'chartOptions', 'wellSelection', 'operatorSelection', 'productionType', 'reservesType', 'hierarchy')
)(Workspace)



