// react & redux
import React from 'react'

export const OperatorItem = ({ operator, handleClick }) => {
  function toggle() {
    handleClick(operator.name)
  }

  return (
    <div
      className={'operator-item' + (operator.selected ? ' active' : '')}
      onClick={toggle}
    ><span className="swatch" style={{'backgroundColor':operator.color}}></span>
      {operator.name}
    </div>
  )
}

export default OperatorItem



