// react & redux
import React from 'react'

// core classes & libs
import ChartWellSelectable from './ChartWellSelectable'
import autobind from 'autobind-decorator'

@autobind class ChartOperator extends ChartWellSelectable {
  showSeriesFromState(operatorSelection) {
    this.chart.series.forEach(series => {
      let showing = operatorSelection.includes(series.name)
      series.setVisible(showing, false)
    })

    this.chart.redraw(false)
  }

    // subsequent renders loads from new props
  componentWillReceiveProps(nextProps) {
    super.componentWillReceiveProps(nextProps)

    let s2 = nextProps.state
    let s1 = this.props.state

    if (s1.operatorSelection !== s2.operatorSelection) {
      this.showSeriesFromState(s2.operatorSelection)
    }
  }

  chartLoaded(chart) {
    super.chartLoaded(chart)
    let { operatorSelection } = this.props.state

    // this calls a redraw, so none needed
    this.showSeriesFromState(operatorSelection)
  }
}

export default ChartOperator



