import ReduxRegistry from 'redux-registry'
import { List, Map, fromJS } from 'immutable'

let register = new ReduxRegistry()

register
  .setInitialState(fromJS({
    limits: {
      date: {
        min: undefined,
        max: undefined
      }
    }
  }))
  .add({
    alias: 'setDateLimits',
    create: (min, max) => ({
      type: 'SET_DATE_LIMITS',
      min,
      max
    }),
    reduce: (state, action) => state.setIn(['limits', 'date'], Map({ min: action.min, max: action.max }))
  })
  .add({
    alias: 'resetDateLimits',
    create: () => ({
      type: 'RESET_DATE_LIMITS'
    }),
    reduce: (state, action) => state.setIn(['limits', 'date'], Map({ min: undefined, max: undefined }))
  })

export default register



