// react & redux
import React from 'react'

// core classes & libs
import ChartZoomable from './ChartZoomable'
import autobind from 'autobind-decorator'
import config from './configs/PortfolioVsAveragePortf.config'
import API from '../../lib/api-store'

@autobind class PortfolioVsAveragePortf extends ChartZoomable {
  constructor(props) {
    super(props)

    super.loadConfig(config, {
      'yAxis.title.text': `${props.seriesName} [${props.units}]`,
    })
  }

  loadData() {
    let { seriesName, phase, units } = this.props

    this.setState({ isLoading: true })

    API.get('?options=utcdates').then(data => {
      if (!this._isMounted) return false

      // transform data from API
      let seriesData = { [seriesName]: [] }

      data.rate_array.forEach(i => {
        seriesData[seriesName].push({ x: i.time, y: i[phase], units: units })
      })

      // inject transformed data into state.config
      this.setState({
        isLoading: false,
        config: this.state.config.update('series', all => all.map(
          series => series.set('data', seriesData[series.get('name')])
        ))
      })
    })
  }
}

export default PortfolioVsAveragePortf



