// react & redux
import React from 'react'

// core classes & libs
import ChartZoomable from './ChartZoomable'
import autobind from 'autobind-decorator'
import config from './configs/PortfolioDrillHistory.config'
import API from '../../lib/api-store'

@autobind class PortfolioDrillHistory extends ChartZoomable {
  constructor(props) {
    super(props)

    super.loadConfig(config)
  }

  loadData() {
    this.setState({ isLoading: true })

    API.get('?options=utcdates', (err, data) => {
      if (err || !this._isMounted) return false

      // transform data from API
      let seriesData = { Active: [], Inactive: [] }

      data.active_count_array.forEach(i => {
        seriesData.Active.push({ x: i.time, y: i.activeWells})
        seriesData.Inactive.push({ x: i.time, y: i.inactiveWells})
      })

      // inject transformed data into state.config
      this.setState({
        isLoading: false,
        config: this.state.config.update('series', all => all.map(
          series => series.set('data', seriesData[series.get('name')])
        ))
      })
    })
  }
}

export default PortfolioDrillHistory



