// react & redux
import React from 'react'
import API from '../../lib/api-store'
import autobind from 'autobind-decorator'

// core libs etc
import async from 'async'

// leaflet specifics
import L from 'leaflet'
import BingLayer from 'leaflet-bing-layer'
import LeafletMap from './LeafletMap'

// DEFINE CLASS
@autobind class OperatorMap extends LeafletMap {
  constructor(props) {
    super(props)
  }

  // API CALL ADDS WELL LOCATION LAYER AND APPLIES STYLING TO MAP
  loadData() {
    let component = this

    let geoJSONpath = this.geoJSONPath()
    if (!geoJSONpath) return false

    this.setState({ isLoading: true })

    async.parallel({
      geo: API.async(geoJSONpath),
      wells: API.async('wells'),
      operators: API.async('operators')
    }, (err, data) => {
      this.setState({
        isLoading: false,
        wells: data.wells,
        operators: data.operators,
        geojson: data.geo
      })
    })
  }

  popupAttributes() {
    return [
      'status',
      'operator',
      'field',
      'formation',
      'reservoir',
      'drill_type',
      'spud_date'
    ]
  }

  createColors() {
    let { operators } = this.state
    let colors = {}

    operators.forEach(o => {
      colors[o.name.replace(/,/g, '')] = o.color
    })

    return colors
  }

  colorBy(well) {
    this.colors = this.colors || this.createColors()

    return this.colors[well.operator]
  }

  filterFeatures(feature, layer) {
    let well = this.state.wells[feature.properties.wUID]
    let { operatorSelection } = this.props.state

    return operatorSelection.includes(well.operator)
  }
}

export default OperatorMap



