// react & redux
import React from 'react'

// child components
import TypeCurveTotal from '../Charts/TypeCurveTotal'
import TypeCurveYearly from '../Charts/TypeCurveYearly'

const TypeCurves = ({ state, actions }) => {
  const portfolio = state.portfolio.get('selected');

  return (
    <div className="page type-curves-page padded-children">
      <TypeCurveTotal state={state} actions={actions} portfolio={portfolio} units = "STB/D" seriesName = "Normalized First Year Type Curve Oil" yAxisTitle = "Normalized Oil Rate [STB/D]" normalizedIndex = {2} rawIndex = {0}  />
      <TypeCurveTotal state={state} actions={actions} portfolio={portfolio} units = "MSCF/D" seriesName = "Normalized First Year Type Curve Gas" yAxisTitle = "Normalized Gas Rate [MSCF/D]" normalizedIndex = {3} rawIndex = {1}  />
      <TypeCurveYearly state={state} actions={actions} portfolio={portfolio} units = "STB/D" yAxisTitle = "Normalized Oil Rate [STB/D]" chartTitle = "" phase = "OPR" chartTitle = "Normalized Oil Rate by Drill Year"/>
      <TypeCurveYearly state={state} actions={actions} portfolio={portfolio} units = "MSCF/D" yAxisTitle = "Normalized Gas Rate [MSCF/D]" chartTitle = "" phase = "GPR" chartTitle = "Normalized Gas Rate by Drill Year"/>
    </div>
  )
}

export default TypeCurves



