// react & redux
import React from 'react'

const Expander = ({ toggleHandler, expanded }) => {
  return (
    <strong className={'expander' + (expanded ? ' expanded' : '')} onClick={toggleHandler} title={ expanded ? 'shrink this panel' : 'expand this panel'}></strong>
  )
}

export default Expander



