import ReduxRegistry from 'redux-registry'

let register = new ReduxRegistry()

register
  .setInitialState('EUR')
  .add({
    alias: 'toggle',
    create: () => ({
      type: 'TOGGLE_RESERVES_TYPE'
    }),
    reduce: function(state, action) {
      return state === 'EUR' ? 'PDP' : 'EUR'
    }
  })

export default register



