// react & redux
import React from 'react'
import autobind from 'autobind-decorator'
import API from '../../lib/api-store'
import { Router } from 'react-router';

// helpers
import classNames from 'classnames'

@autobind class LoginForm extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      isVerifying: false,
      error: undefined
    }
  }

  static get contextTypes() {
    return {
      router: React.PropTypes.object.isRequired,
    }
  }

  componentDidMount() {
    this._isMounted = true
  }

  componentWillUnmount() {
    this._isMounted = false
  }

  attemptLogin(e) {
    e.preventDefault()

    let userid = this.refs.userid.value
    let userpwd = this.refs.userpwd.value

    this.setState({ isVerifying: true, error: false })
    this.forceUpdate()

    API
      .auth({ userid, userpwd })
      .then(user => {
        if (this._isMounted) {
          this.props.loginAction(user)
          this.context.router.push('/portfoliooverview')
        }
      })
      .catch(error => {
        if (this._isMounted) {
          console.log('Login Error', error.message)
          this.setState({ isVerifying: false, error })
          this.forceUpdate()
        }
      })
  }

  render() {
    let { error, isVerifying } = this.state

    return (
      <form className={classNames('login-form', { error })} onSubmit={this.attemptLogin}>
        <label>
          <span>User ID <small>(usually an email address)</small></span>
          <input type="text" ref="userid" placeholder="Enter a user ID" />
        </label>

        <label>
          <span>Password</span>
          <input type="password" ref="userpwd" placeholder="Enter your password" />
        </label>

        <button className={isVerifying ? 'verifying' : ''}>{ isVerifying ? 'Verifying...' : 'Login' }</button>
      </form>
    )
  }
}

export default LoginForm



