// react & redux
import React from 'react'

//child components
import OperatorEURbyDepth from '../Charts/OperatorEURbyDepth'
import OperatorDepthbySpud from '../Charts/OperatorDepthbySpud'
import OperatorEURandOilPrice from '../Charts/OperatorEURandOilPrice'
import OperatorMap from '../Maps/OperatorMap'
import OperatorSelectionPanel from '../Panels/OperatorSelectionPanel'

const Operators = ({ state, actions, portfolio }) => {
  let props = { state, actions, portfolio }

  let oilGasLookUp = {
    'OILEUR': {
      dataToPlot: 'oil_eur',
      yLabel: 'Oil EUR',
      units: 'MSTB',
      conversionFactor: 1e3
    },
    'GASEUR': {
      dataToPlot: 'gas_eur',
      yLabel: 'Gas EUR',
      units: 'MMSCF',
      conversionFactor: 1e3
    },
    'OILPDP': {
      dataToPlot: 'oil_pdp',
      yLabel: 'Oil PDP',
      units: 'MSTB',
      conversionFactor: 1e3
    },
    'GASPDP': {
      dataToPlot: 'gas_pdp',
      yLabel: 'Gas PDP',
      units: 'MMSCF',
      conversionFactor: 1e3
    }
  }

  return (
    <div className="page operators-page">
      <OperatorSelectionPanel {...props} />
      <div className="panels">
        <OperatorEURbyDepth {...props} oilGasLookUp={oilGasLookUp} />
        <OperatorDepthbySpud {...props} oilGasLookUp={oilGasLookUp} />
        <OperatorEURandOilPrice {...props} oilGasLookUp={oilGasLookUp} />
        <OperatorMap {...props} />
      </div>
    </div>
  )
}

export default Operators




