// react & redux
import React from 'react'
import { connect } from 'react-redux'

// reducers & actions
import { automapState, automapActions } from '../../../redux/helpers'

// child components
import StateGroup from './StateGroup'
import StateAction from './StateGroup/StateAction'
import PortfolioSelect from './PortfolioSelect'

export const WorkspaceToolbar = ({ actions, state }) => {
  let gasVsOil = [
    { label: 'oil', value: 'OIL' },
    { label: 'gas', value: 'GAS' }
  ]

  let eurVsPdp = [
    { label: 'eur', value: 'EUR' },
    { label: 'pdp', value: 'PDP' }
  ]

  let selectVsZoom = [
    { label: 'select', value: 'SELECT' },
    { label: 'zoom', value: 'ZOOM' }
  ]

  let portfolio = state.portfolio.get('selected')

  return (
    <div className="workspace-toolbar">
      <PortfolioSelect className="portfolio-select" state={state} actions={actions} portfolio={portfolio} />

      <div className="filters">
        <StateAction
          active={state.wellSelection.size}
          action={actions.wellSelection.clearWells}>
          Clear Well Selection
        </StateAction>
        <StateAction
          active={state.chartOptions.getIn(['limits', 'date', 'min'])}
          action={actions.chartOptions.resetDateLimits}>
          Reset Zoom
        </StateAction>
        <StateGroup options={gasVsOil} state={state.productionType} action={actions.productionType.toggle} active={true} />
        <StateGroup options={eurVsPdp} state={state.reservesType} action={actions.reservesType.toggle} active={true} />
        {/*<StateGroup options={selectVsZoom} state={state.selectionType} action={actions.selectionType.toggle} />
        */}

      </div>
    </div>
  )
}

export default WorkspaceToolbar

export const WorkspaceToolbarContainer = connect(
  automapState('portfolio','selectionType','productionType', 'reservesType', 'chartOptions', 'wellSelection', 'operatorSelection', 'hierarchy', 'user'),
  automapActions('portfolio','selectionType','productionType', 'reservesType', 'chartOptions', 'wellSelection', 'operatorSelection', 'hierarchy', 'user')
)(WorkspaceToolbar)



