	export const extend = (baseConfig, ...configs) => {
		configs.forEach(config => {
			Object.keys(config).forEach(fullPath => {
				let paths = fullPath.split('.')
				let finalPath = paths.pop() // pop final node off paths
				let node = baseConfig

				paths.forEach(path => {
					node = (node[path] = node[path] || {})
				})

				// assign value at final node spot
				node[finalPath] = config[fullPath]
			})
		})

		return baseConfig;
	}



