// react & redux
import React from 'react'
import ReactHighcharts from 'react-highcharts'

// core classes & libs
import { fromJS } from 'immutable'
import config from './configs/HistoryGroupedPie.config'
import async from 'async'

// child components
import API from '../../lib/api-store'

export const HistoryGroupedPie = React.createClass({
  getInitialState () {
    return {
      isLoading: false,
      chartConfig: fromJS(config) // converts chartConfig into Immutable structure
    }
  },

  // first render loads from state
  componentDidMount: function() {
    this._isMounted= true
    this.loadProductionData(this.props.portfolio)
  },

  componentWilLUnmount() {
    this._isMounted = false
  },

  // subsequent renders loads from new props
  componentWillReceiveProps: function(nextProps) {
    if (nextProps.portfolio !== this.props.portfolio) {
      this.loadProductionData(nextProps.portfolio)
    }
  },

  // only update component on portfolio change
  shouldComponentUpdate: function(nextProps) {
    return nextProps.portfolio !== this.props.portfolio
  },

  loadProductionData: function(portfolio) {
    if (!portfolio) {
      return false
    }

    let component = this
    const { chart } = this.refs.chart
    let { yValue, yPercent, units, chartTitle } = this.props

    let asyncCalls = []
    let callNames = ['field=100', 'field=101', 'field=102']

    callNames.forEach(name => {
      asyncCalls.push(function(callback) {
        API.get('http://localhost:3001/api/portfolios/' + portfolio + '/production?' + name + '&options=utcdates', (err, data) => {
          callback(null, data)
        })
      })
    })

    asyncCalls.unshift(function(callback){
      API.get('http://localhost:3001/api/portfolios/' + portfolio + '/wells?options=utcdates', (err, data) => {
        callback(null, data);
      })
    })

    asyncCalls.unshift(function(callback){
      API.get('http://localhost:3001/api/portfolios/' + portfolio + '/spaObjects', (err, data) => {
        callback(null, data);
      })
    })

    async.parallel(asyncCalls, function(err, data) {
      if (err || !component._isMounted) return false

      let fieldObjects = {};

      data[0].forEach(i => {
        if (i.type === "FIELD") {
          fieldObjects[i.object_num] = i;
        }
      })

      let series = { data: [], name: chartTitle }

      if (chartTitle === 'Oil Rate'){
        // Sum over all last production rate
        let sumPortfRate = 0;
        data.slice(2).forEach(i => {
          // TODO: Check date of this value -- needs to be xMax of plot zoom level... (or end of portfolio life by default)
          sumPortfRate += i.production[i.production.length - 1].oil
        })

        data.slice(2).forEach(i => {
          series.data.push({
            name: fieldObjects[i.field_entity_id].object_name,
            y: i.production[i.production.length - 1].oil / sumPortfRate,
            type: 'pie',
            units,
            color: fieldObjects[i.field_entity_id].color,
            dataToDisplay: i.production[i.production.length - 1].oil,
            hoverbox: []
          })
        })
        chart.addSeries(series);
      } else {
        let entityCum = []
        data.slice(2).forEach(i => {
          let fieldCum = 0
          i.wells.forEach(j => {
            fieldCum += data[1][j][yValue]
          })
          entityCum.push(fieldCum)
        })
        let totalCum = entityCum.reduce( (a, b) => { return a + b }, 0)

        data.slice(2).forEach((i, index) => {
          series.data.push({
            name: fieldObjects[i.field_entity_id].object_name,
            y: entityCum[index] / totalCum,
            type: 'pie',
            units,
            color: fieldObjects[i.field_entity_id].color,
            dataToDisplay: entityCum[index],
            hoverbox: []
          })
        })
        chart.addSeries(series)
      }
      chart.setTitle({text: chartTitle})
      chart.redraw()
    })
  },

  render() {
    console.log('HistoryGroupedPie:rendering...');
    return (
      <div className="panel chart operator-eur-by-depth">
        <ReactHighcharts config={this.state.chartConfig.toJS()} ref="chart"></ReactHighcharts>
      </div>
    )
  }
})

export default HistoryGroupedPie



