// react & redux
import React from 'react'
import { Link } from 'react-router'

export const WorkspaceNav = React.createClass({
  render() {
    return (
      <div className="workspace-nav">
        <div className="group">
          <Link to="/portfoliooverview" activeClassName="active">Portfolio Overview</Link>
          <Link to="/topfields" activeClassName="active">Top Fields</Link>
          <Link to="/summary" activeClassName="active">Summary</Link>
          <Link to="/history" activeClassName="active">History</Link>
          <Link to="/reserves" activeClassName="active">Reserves</Link>
          <Link to="/operators" activeClassName="active">Operators</Link>
          <Link to="/portfvsavg" activeClassName="active">Portfolio Vs. Average</Link>
          <Link to="/typecurves" activeClassName="active">Type Curves</Link>
        </div>
      </div>
    )
  }
})

export default WorkspaceNav



