// react & redux
import React from 'react'

// core classes & libs
import ChartHoverSync from './ChartHoverSync'
import autobind from 'autobind-decorator'
import config from './configs/TypeCurveYearly.config'
import API from '../../lib/api-store'

@autobind class TypeCurveYearly extends ChartHoverSync {
  constructor(props) {
    super(props)

    super.loadConfig(config, {
      'yAxis.title.text': `${props.yAxisTitle}`,
    })
  }

  loadData() {
    let { phase, units, chartTitle } = this.props;

    this.setState({ isLoading: true })

    API.get('?options=utcdates', (err, data) => {
      if (err || !this._isMounted) return false

      let component = this
      // transform data from API
      let { type_curves } = data
      let seriesData = [];

      if (phase === 'OPR'){
        let year1 = type_curves[4].typeCurveName.slice(type_curves[4].typeCurveName.indexOf("2"))
        let year2 = type_curves[6].typeCurveName.slice(type_curves[6].typeCurveName.indexOf("2"))
        let year3 = type_curves[8].typeCurveName.slice(type_curves[8].typeCurveName.indexOf("2"))
        let year4 = type_curves[10].typeCurveName.slice(type_curves[10].typeCurveName.indexOf("2"))
        let year5 = type_curves[12].typeCurveName.slice(type_curves[12].typeCurveName.indexOf("2"))
        let yearArray1 = { data: [], name: year1, color: '#99FF99' }
        let yearArray2 = { data: [], name: year2, color: '#00FF00' }
        let yearArray3 = { data: [], name: year3, color: '#52ab7f' }
        let yearArray4 = { data: [], name: year4, color: '#009900' }
        let yearArray5 = { data: [], name: year5, color: '#006600' }
        for (let iMonth = 0; iMonth < 12; iMonth++) {
          yearArray1.data.push({
            x: type_curves[5].data[iMonth].x,
            y: type_curves[5].data[iMonth].y,
            rawValue: type_curves[4].data[iMonth].y,
            year: year1,
            units
          })
          yearArray2.data.push({
            x: type_curves[7].data[iMonth].x,
            y: type_curves[7].data[iMonth].y,
            rawValue: type_curves[6].data[iMonth].y,
            year: year2,
            units
          })
          yearArray3.data.push({
            x: type_curves[9].data[iMonth].x,
            y: type_curves[9].data[iMonth].y,
            rawValue: type_curves[8].data[iMonth].y,
            year: year3,
            units
          })
          yearArray4.data.push({
            x: type_curves[11].data[iMonth].x,
            y: type_curves[11].data[iMonth].y,
            rawValue: type_curves[10].data[iMonth].y,
            year: year4,
            units
          })
          yearArray5.data.push({
            x: type_curves[13].data[iMonth].x,
            y: type_curves[13].data[iMonth].y,
            rawValue: type_curves[12].data[iMonth].y,
            year: year5,
            units
          })
        }
        seriesData.push(yearArray1)
        seriesData.push(yearArray2)
        seriesData.push(yearArray3)
        seriesData.push(yearArray4)
        seriesData.push(yearArray5)
      } else {
        let year1 = type_curves[14].typeCurveName.slice(type_curves[14].typeCurveName.indexOf("2"))
        let year2 = type_curves[16].typeCurveName.slice(type_curves[16].typeCurveName.indexOf("2"))
        let year3 = type_curves[18].typeCurveName.slice(type_curves[18].typeCurveName.indexOf("2"))
        let year4 = type_curves[20].typeCurveName.slice(type_curves[20].typeCurveName.indexOf("2"))
        let year5 = type_curves[22].typeCurveName.slice(type_curves[22].typeCurveName.indexOf("2"))
        let yearArray1 = { data: [], name: year1, color: '#FFCCCC' }
        let yearArray2 = { data: [], name: year2, color: '#FF6666' }
        let yearArray3 = { data: [], name: year3, color: '#FF0000' }
        let yearArray4 = { data: [], name: year4, color: '#CC0000' }
        let yearArray5 = { data: [], name: year5, color: '#990000' }
        for (let iMonth = 0; iMonth < 12; iMonth++) {
          yearArray1.data.push({
            x: type_curves[15].data[iMonth].x,
            y: type_curves[15].data[iMonth].y,
            rawValue: type_curves[14].data[iMonth].y,
            year: year1,
            units
          })
          yearArray2.data.push({
            x: type_curves[17].data[iMonth].x,
            y: type_curves[17].data[iMonth].y,
            rawValue: type_curves[16].data[iMonth].y,
            year: year2,
            units
          })
          yearArray3.data.push({
            x: type_curves[19].data[iMonth].x,
            y: type_curves[19].data[iMonth].y,
            rawValue: type_curves[18].data[iMonth].y,
            year: year3,
            units
          })
          yearArray4.data.push({
            x: type_curves[21].data[iMonth].x,
            y: type_curves[21].data[iMonth].y,
            rawValue: type_curves[20].data[iMonth].y,
            year: year4,
            units
          })
          yearArray5.data.push({
            x: type_curves[23].data[iMonth].x,
            y: type_curves[23].data[iMonth].y,
            rawValue: type_curves[22].data[iMonth].y,
            year: year5,
            units
          })
        }
        seriesData.push(yearArray1)
        seriesData.push(yearArray2)
        seriesData.push(yearArray3)
        seriesData.push(yearArray4)
        seriesData.push(yearArray5)
      }



      console.log(seriesData)

      component.setState({
        isLoading: false,
        config: component.state.config.update('series', s => seriesData)
        .setIn(['title', 'text'], chartTitle)
      })

    })
  }
}

export default TypeCurveYearly



