// react & redux
import React from 'react'
import { Link } from 'react-router'

const ProductbarActions = ({ user, logoutAction }) => {
  const loginLink = user
    ? <Link to="/logout" className="logout" onClick={logoutAction}><div>Log Out</div></Link>
    : <Link to="/login" className="login"><div>Log In</div></Link>

  return (
    <div className="actions">
      <Link to="/help" className="help"><div>Help</div></Link>
      { loginLink }
    </div>
  )
}

export default ProductbarActions



