// react & redux
import React from 'react'

// core classes & libs
import ChartOperator from './ChartOperator'
import autobind from 'autobind-decorator'
import config from './configs/OperatorEURandOilPrice.config'
import API from '../../lib/api-store'
import async from 'async'

@autobind class OperatorEURandOilPrice extends ChartOperator {
  constructor(props) {
    super(props)

    super.loadConfig(config)
  }

  loadData(nextProps) {
    let component = this
    let props = nextProps || this.props
    let { oilGasLookUp } = props
    let structToPlot = oilGasLookUp[props.state.productionType + props.state.reservesType]
    this.setState({ isLoading: true })

    async.parallel({
      operators: API.async('operators'),
      wells: API.async('wells?options=utcdates')
    },
    function(err, data) {
      if (err || !component._isMounted) return false

      let { operators, wells } = data
      let seriesData = []

      operators.forEach(operator => {
        let series = { data: [], name: operator.name, color: operator.color }

        operator.wells.forEach(wUID => {
          let well = wells[wUID]
          let depth = well.total_depth
          if (depth === 0) {
            depth = "N/A"
          } else {
            depth = depth + " [ft]"
          }

          series.data.push({
            x: well.spud_date,
            y: well[structToPlot.dataToPlot] / structToPlot.conversionFactor,
            wellName: well.well_name,
            operator: well.operator,
            yUnitLabel: structToPlot.yLabel,
            units: structToPlot.units,
            wUID: well.wUID,
            depth
          })
        })

        seriesData.push(series)
      })

      // inject transformed data into state.config
      console.log('RUTH LOOK HERE: ', component)
      component.setState({
        isLoading: false,
        config: component.state.config.set('series', seriesData)
        .setIn(['yAxis', 0, 'title', 'text'], structToPlot.yLabel + ' [' + structToPlot.units + ']')
      })

      let { operatorSelection } = component.props.state

      // this calls a redraw, so none needed
      component.showSeriesFromState(operatorSelection)
    })
  }

  shouldComponentUpdate(nextProps, nextState) {
    return super.shouldComponentUpdate(nextProps, nextState) ||
      (nextProps.state.productionType !== this.props.state.productionType) ||
      (nextProps.state.reservesType !== this.props.state.reservesType)
  }

  componentWillReceiveProps(nextProps) {
    super.componentWillReceiveProps(nextProps)
    if (nextProps.state.productionType !== this.props.state.productionType ||
        nextProps.state.reservesType !== this.props.state.reservesType) {
      this.loadData(nextProps)
    }
  }
}

export default OperatorEURandOilPrice



