// react & redux
import React from 'react'
import autobind from 'autobind-decorator'

// core classes & libs
import { fromJS } from 'immutable'
import { extend } from './configs/helpers'
import async from 'async'
import API from '../../lib/api-store'

// child components
import Chart from './Chart'

@autobind class ChartEntityFilterable extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      isLoading: false
    }
  }

  loadConfig(base, ...configs) {
    let component = this;

    this.state.config = fromJS(extend(base, {}, ...configs))
  }

  // first render loads from state
  componentWillMount() {
    this.loadData(this.props.state.hierarchy.get('entities'))
  }

  componentDidMount() {
    this._isMounted = true
  }

  componentWillUnmount() {
    this._isMounted= false
  }

  // subsequent renders loads from new props
  componentWillReceiveProps(nextProps) {
    const s1 = this.props.state
    const s2 = nextProps.state
    const { chart } = this

    if (nextProps.portfolio !== this.props.portfolio || s1.hierarchy !== s2.hierarchy) {
      this.loadData(s2.hierarchy.get('entities'))
    }

    if (s1.hierarchy !== s2.hierarchy) return true

    // receive state wellSelection and set internally
    if (s2.wellSelection !== s1.wellSelection) {
      this.selectWellsFromState(s2.wellSelection)
    }
  }

  // only update component on portfolio change
  shouldComponentUpdate(nextProps, nextState) {
    let s1 = this.state
    let s2 = nextState

    if (nextProps.portfolio !== this.props.portfolio) return true
    if (s1.hierarchy !== s2.hierarchy) return true
    if (s1.config !== s2.config) return true
    if (s1.isLoading !== s2.isLoading) return true

    return false
  }

  getSeriesList(entities) {
    return entities.toJS().map(e => {
      let simplified = {
        name: e.name,
        color: e.color,
        where: [e.type+'='+e.id]
      }

      if (e.parent) {
        simplified.where.push(e.parent.type+'='+e.parent.id)
      }

      simplified.params = simplified.where.join('&')

      return simplified
    })
  }

  generateAsyncCalls(groups, type) {
    let map = {}

    groups.forEach(group => {
      map[group.name] = API.async(type + '?options=utcdates&' + group.params)
    })

    return map
  }

  getAsyncData(groups, type, callback) {
    this.setState({ isLoading: true })

    let asyncCalls = this.generateAsyncCalls(groups, type)

    async.parallel(asyncCalls, (err, data) => {
      callback(err, data)
    })
  }

  getProductionData(groups, callback) {
    this.getAsyncData(groups, 'production', (err, data) => {
      callback(err, data)
    })
  }

  getForecastData(groups, callback) {
    this.getAsyncData(groups, 'forecast', (err, data) => {
      callback(err, data)
    })
  }

  loadData(entities) {
    let component = this
    let { phaseToPlot, units } = this.props
    let groups = this.getSeriesList(entities)

    this.getProductionData(groups, (err, data) => {
      let seriesData = groups.map(group => ({
        name: group.name,
        color: group.color,
        data: data[group.name].production.map(p => ({
          x: p.date,
          y: p.oil,
          units
        }))
      }))

      console.log('SERIES DATA', seriesData)

      component.setState({
        isLoading: false,
        config: component.state.config.set('series', seriesData)
      })
    })
  }

  chartLoaded(chart) {
    this.chart = chart
  }

  render() {
    let { isLoading, config } = this.state
    let { parent } = this.props

    return (
      <Chart parent={parent} isLoading={isLoading} config={config.toJS()} callback={this.chartLoaded} />
    )
  }
}

export default ChartEntityFilterable



