// react & redux
import React from 'react'

// core classes & libs
import config from './configs/TopFieldsPieChart.config'
import async from 'async'
import { extend } from './configs/helpers'

// child components
import API from '../../lib/api-store'
import Chart from './Chart'

export const TopFieldsPieChart = React.createClass({
  getInitialState() {
    return {
      config: fromJS(extend(config)),
      isLoading: false,
      listView: [],
    }
  },

  // first render loads from state
  componentDidMount() {
    this._isMounted = true
    this.loadData()
  },

  componentWillUnmount() {
    this._isMounted = false
  },

  // subsequent renders loads from new props
  componentWillReceiveProps: function(nextProps) {
    if (nextProps.portfolio !== this.props.portfolio) {
      this.loadData()
    }
  },

  loadData() {
    let component = this
    let { yPercent, yValue, units, chartTitle } = this.props

    // setup for async call
    this.setState({ isLoading: true })

    async.parallel({
      topFields: API.async('fieldstats'),
      analyzedObjects: API.async('spaObjects')
    }, function(err, data){
      if (err || !component._isMounted) return false

      let { topFields, analyzedObjects } = data
      let fieldObjects = {};
        analyzedObjects.forEach(i => {
          if (i.type === "FIELD") {
            fieldObjects[i.object_name] = { color: i.color };
          }
        })

      let seriesData = { data: [], name: chartTitle }
      topFields.slice(1).forEach(field => {
        if (field.fieldName === 'Total') return

        seriesData.data.push({
          name: field.fieldName,
          y: field[yPercent],
          type: 'pie',
          units,
          color: fieldObjects[field.fieldName].color,
          dataToDisplay: field[yValue]
        })
      })
      // Populate table list
      let listView = [];

      topFields.slice(1).forEach((field, index) => {
        listView.push({ field: field.fieldName, value: field[yValue] })
      })

      component.setState({
        isLoading: false,
        listView,
        config: component.state.config.update('series', s => [seriesData])
      })
    })
  },

  chartLoaded(chart) {
    this.chart = chart
  },

  render() {
    let { units, chartTitle } = this.props

    console.log('TopFieldsPieChart:rendering');
    return (
      <div className="panel top-fields">
        <h2>{ chartTitle } <small>{ units }</small></h2>
        <Chart isLoading={this.state.isLoading} config={this.state.config.toJS()} callback={this.chartLoaded} />

        <table className="list-view">
          <tbody>
            {
              this.state.listView.sort((a, b) => a.value > b.value ? -1 : 1).map((row, index) =>
                <tr key={index}><th>{ row.field }</th><td>{ row.value }</td></tr>
              )
            }
          </tbody>
        </table>
      </div>
    )
  }
})

export default TopFieldsPieChart



