import ReduxRegistry from 'redux-registry'
import { List, Map } from 'immutable'

window.List = List
window.Map = Map

let register = new ReduxRegistry()

register
  .setInitialState(List())
  .add({
    alias: 'setOperators',
    create: operators => ({
      type: 'SET_OPERATORS',
      operators
    }),
    reduce: (state, action) => List(action.operators)
  })
  .add({
    alias: 'clearOperators',
    create: () => ({ type: 'CLEAR_OPERATOR_SELECTION' }),
    reduce: state => List()
  })
  .add({
    alias: 'removeOperator',
    create: function(operator) {
      return {
        type: 'REMOVE_OPERATOR',
        operator
      }
    },
    reduce: (state, action) => state.filter(o => o !== action.operator)
  })
  .add({
    alias: 'addOperator',
    create: function(operator) {
      return {
        type: 'ADD_OPERATOR',
        operator
      }
    },
    reduce: (state, action) => state.push(action.operator)
  })
;

export default register



