// react & redux
import React from 'react'

// core classes & libs
import ChartZoomable from './ChartZoomable'
import autobind from 'autobind-decorator'
import config from './configs/PortfolioProductionHistory.config'
import API from '../../lib/api-store'

@autobind class PortfolioProductionHistory extends ChartZoomable {
  constructor(props) {
    super(props)

    super.loadConfig(config)
  }

  loadData() {
    this.setState({ isLoading: true })

    API.get('?options=utcdates').then(data => {
      if (!this._isMounted) return false

      // transform data from API
      let seriesData = { Oil: [], Gas: [], Water: [] }

      data.rate_array.forEach(i => {
        seriesData.Oil.push({ x: i.time, y: i.OPR, units: 'STB/D' })
        seriesData.Gas.push({ x: i.time, y: i.GPR, units: 'MSCF/D' })
        seriesData.Water.push({ x: i.time, y: i.WPR, units: 'BBL/D' })
      })

      this.setState({
        isLoading: false,
        config: this.state.config.update('series', all => all.map(
          series => series.set('data', seriesData[series.get('name')])
        ))
      })
    })
  }
}

export default PortfolioProductionHistory



