import ReduxRegistry from 'redux-registry'
import { List, Map, Set } from 'immutable'

let register = new ReduxRegistry()

window.rr = register

register
  .setInitialState(List())
  .add({
    alias: 'setWells',
    create: wells => ({
      type: 'SET_WELL_SELECTION',
      wells
    }),
    reduce: (state, action) => List(action.wells)
  })
  .add({
    alias: 'clearWells',
    create: () => ({ type: 'CLEAR_WELL_SELECTION' }),
    reduce: state => List()
  })
  .add({
    alias: 'removeWell',
    create: function(well) {
      return {
        type: 'REMOVE_WELL_SELECTION',
        well
      }
    },
    reduce: (state, action) => state.filter(w => w !== action.well)
  })
  .add({
    alias: 'addWell',
    create: function(well) {
      return {
        type: 'ADD_WELL_SELECTION',
        well
      }
    },
    reduce: (state, action) => state.includes(action.well) ? state : state.push(action.well)
  })
  .add({
    alias: 'toggleWell',
    create: function(well) {
      return {
        type: 'TOGGLE_WELL_SELECTION',
        well
      }
    },
    reduce: (state, action) => {
      if (state.includes(action.well)) {
        return state.filter(w => w !== action.well)
      } else {
        return state.push(action.well)
      }
    }
  })
;

export default register



