// react & redux
import React from 'react'

// core classes & libs
import { fromJS } from 'immutable'
import { extend } from './configs/helpers'
import autobind from 'autobind-decorator'

// child components
import Chart from './Chart'

@autobind class ChartLoader extends React.Component {
  constructor(props) {
    super(props)

    let component = this;

    this.state = {
      isLoading: false,
      expanded: false,
    }
  }

  loadConfig(base, ...configs) {
    let component = this;

    this.state.config = fromJS(extend(base, {
      'xAxis.events.setExtremes': function(e) {
        component.setExtremes(e.min, e.max)
      }
    }, ...configs))
  }

  setExtremes(min, max) {
    let { chartOptions } = this.props.state
    let stateRange = chartOptions.getIn(['limits', 'date'])

    if (stateRange.get('min') !== min || stateRange.get('max') !== max) {
      this.props.actions.chartOptions.setDateLimits(min, max)
    }
  }

  // first render loads from state
  componentDidMount() {
    this._isMounted = true
    this.loadData()
  }

  componentWillUnmount() {
    this._isMounted = false
  }

  // subsequent renders loads from new props
  componentWillReceiveProps(nextProps) {
    const s1 = this.props.state
    const s2 = nextProps.state

    if (nextProps.portfolio !== this.props.portfolio) {
      this.loadData()
    }

    if (s1.chartOptions !== s2.chartOptions) {
      let date = s2.chartOptions.getIn(['limits', 'date'])

      if (date.get('min') && this.chart) {
        this.chart.xAxis[0].setExtremes(date.get('min'), date.get('max'), true, false)
      } else {
        this.chart.xAxis[0].setExtremes(null, null, true, false)
      }
    }
  }

  // only update component on portfolio change
  shouldComponentUpdate(nextProps, nextState) {
    let s1 = this.state
    let s2 = nextState

    if (nextProps.portfolio !== this.props.portfolio) return true
    if (s1.config !== s2.config) return true
    if (s1.isLoading !== s2.isLoading) return true

    return false
  }

  chartLoaded(chart) {
    this.chart = chart

    let date = this.props.state.chartOptions.getIn(['limits', 'date'])

    if (date.get('min')) {
      this.chart.xAxis[0].setExtremes(date.get('min'), date.get('max'), true, false)
    } else {
      this.chart.zoomOut()
    }
  }

  render() {
    let { isLoading, config, expanded } = this.state

    return (
      <Chart isLoading={isLoading} config={config.toJS()} callback={this.chartLoaded} />
    )
  }
}

export default ChartLoader



