// react & redux
import React from 'react'

//child components
import Image from '../Image'
import OverviewText from '../OverviewText'

const PortfolioOverview = ({ state, actions }) => {
  const portfolio = state.portfolio.get('selected')

  return (
    <div className="page portfolio-overview-page padded-children">
    	<OverviewText portfolio={portfolio} />
      <Image portfolio={portfolio} type={'overview_cross_section'} extension={'png'} />
      <Image portfolio={portfolio} type={'overview_field_map'} extension={'png'} />
      <Image portfolio={portfolio} type={'reserves'} extension={'png'} />
    </div>
  )
}

export default PortfolioOverview



