// react & redux
import React from 'react'
import classNames from 'classnames'


const TextPanel = ({ text, className, isLoading }) => {
	console.log('TextPanel render', text.split('\n'))
	let transformed = text.split('\\n').map((line, index) =>
			<li key={index}>{ line.replace(/\*/g,'') }</li>
		)

  return (
    <div className={classNames(className, { isLoading: isLoading })}><ul>{ transformed }</ul></div>
  )
}

export default TextPanel



