// react & redux
import React from 'react'
import autobind from 'autobind-decorator'
import async from 'async'
import API from '../../lib/api-store'

// child components
import EntitySelector from './EntitySelector'
import HierarchySelector from './HierarchySelector'

function characterFrequency(string) {
  let letters = {}

  string.split('').forEach(letter => {
    letters[letter] = (letters[letter] || 0) + 1
  })

  return Object.keys(letters)
          .sort((a, b) => letters[a] < letters[b] ? 1 : (a < b ? -1 : 1))
          .map(letter => [letter, letters[letter]])
}


@autobind class EntitySelectionPanel extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      isLoading: true,
      colors: {},
      entities: {}
    }
  }

  componentWillMount() {
    if (this.props.portfolio) {
      this.loadData()
    }
  }

  componentDidMount() {
    this._isMounted = true
  }

  componentWillUnmount() {
    this._isMounted = false
  }

  bindColors(entities, colors) {
    // console.log('bindColors', entities, colors)
    Object.keys(entities).forEach(type => {
      // console.log('binding type', type)
      entities[type].forEach(entity => {
        // console.log('binding entity', entity, entity.id)
        let colorNode = colors[type][entity.id]

        entity.color = colorNode ? colorNode.color : '#ccc'
      })
    })
  }

  loadData() {
    let component = this

    this.setState({ isLoading: true, entities: undefined, colors: undefined })

    async.parallel({
      entities: API.async('entities'),
      colors: API.async('colors?options=lookup')
    }, (err, data) => {
      if (err || !component._isMounted) return false

      let { colors, entities } = data

      this.bindColors(entities, colors)

      this.setState({
        isLoading: false,
        colors,
        entities
      })
    })
  }

  componentWillReceiveProps(props, state) {
    if (props.portfolio !== this.props.portfolio) {
      this.loadData()
    } else {
      if (state.colors && state.entities) {
        console.log('no longer loading')
        bindColors(state.entities, state.colors)
        this.setState({ isLoading: false })
      }
    }
  }

  render() {
    let { state, actions } = this.props
    let { entities, colors, isLoading } = this.state
    let selectedTypes = state.hierarchy.get('types').toJS()
    let selectedEntities = state.hierarchy.get('entities').toJS()

    let entitySelector = isLoading ? '' : <EntitySelector
                                            isLoading={isLoading}
                                            entities={entities}
                                            colors={colors}
                                            hierarchy={selectedTypes}
                                            selection={selectedEntities}
                                            addHandler={actions.hierarchy.addEntity}
                                            removeHandler={actions.hierarchy.removeEntity}
                                            />

    console.log('EntitySelectionPanel:render')
    return (
      <div className="side-panel entity-selection">
        <div className="handle">
          <span className="title">Group Results By <small>
            {
              selectedTypes.map(entity =>
                <em key={entity}>{entity}</em>
              )
            }
          </small></span>
        </div>
        <div className="content">
          <HierarchySelector
            selected={selectedTypes}
            selectHandler={actions.hierarchy.setTypes}
            />
            { entitySelector }
        </div>
      </div>
    )
  }
}

export default EntitySelectionPanel



