// react & redux
import React from 'react'

// child components
import TopFieldsPieChart from '../Charts/TopFieldsPieChart'

const TopFields = ({ portfolio }) => {
  return (
    <div className="page top-fields-page">
      <TopFieldsPieChart portfolio={portfolio} yPercent="pdpPercent" yValue="pdp" units="MSTB" chartTitle="PDP" />
      <TopFieldsPieChart portfolio={portfolio} yPercent="boeCumPercent" yValue="boeCum" units="MMBOE" chartTitle="BOE Cumulative" />
      <TopFieldsPieChart portfolio={portfolio} yPercent="oilRatePercent" yValue="oilRate" units="STB/D" chartTitle="Oil Rate" />
      <TopFieldsPieChart portfolio={portfolio} yPercent="gasRatePercent" yValue="gasRate" units="MSCF/D" chartTitle="Gas Rate" />
      <TopFieldsPieChart portfolio={portfolio} yPercent="newWellCountPercent" yValue="newWellCount" units="" chartTitle="New Wells" />
    </div>
  )
}

export default TopFields



