import ReduxRegistry from 'redux-registry'
import { Map } from 'immutable'

let register = new ReduxRegistry()

register
  .setInitialState(Map({
    logVsLinear: 'LOG',
    eurVsPdp: 'EUR'
  }))
  .add({
    alias: 'toggleLogVsLinear',
    create: function() {
      return {
        type: 'TOGGLE_LOGVSLINEAR'
      };
    },
    reduce: state => state.update('logVsLinear', a => a === 'LOG' ? 'LINEAR' : 'LOG')
  })
  .add({
    alias: 'toggleEurVsPdp',
    create: function(value) {
      return {
        type: 'TOGGLE_EURVSPDP',
        value
      };
    },
    reduce: state => state.update('eurVsPdp', a => a === 'EUR' ? 'PDP' : 'EUR')
  })

export default register



