// react & redux
import React from 'react'

// core classes & libs
import ChartEntityFilterable from './ChartEntityFilterable'
import autobind from 'autobind-decorator'
import config from './configs/ReservesDecline.config'
import async from 'async'

@autobind class ReservesDecline extends ChartEntityFilterable {
  constructor(props) {
    super(props)

    super.loadConfig(config)
  }

  loadData(entities, nextProps) {
    let component = this
    let props = nextProps || this.props
    let { oilGasLookUp } = props
    let structToPlot = oilGasLookUp[props.state.productionType]
    let groups = this.getSeriesList(entities)

    async.parallel({
      production: cb => this.getProductionData(groups, (err, data) => cb(null, data)),
      forecast: cb => this.getForecastData(groups, (err, data) => cb(null, data))
    }, (err, data) => {
      if (err || !component._isMounted) return false

      let seriesData = []

      groups.forEach(group => {
        // add production data
        seriesData.push({
          name: group.name,
          color: group.color,
          data: data.production[group.name].production.map(p => ({
            x: p.date,
            y: p[structToPlot.dataToPlot],
            units: structToPlot.units
          }))
        })

        // add forecast data
        seriesData.push({
          name: group.name + ' (forecast)',
          color: group.color,
          dashStyle: 'dash',
          data: data.forecast[group.name].forecast.map(p => ({
            x: p.date,
            y: p[structToPlot.dataToPlot],
            units: structToPlot.units
          }))
        })
      })

      component.setState({
        isLoading: false,
        config: component.state.config.set('series', seriesData)
        .setIn(['yAxis', 'title', 'text'], structToPlot.yLabel + ' [' + structToPlot.units + ']')
      })
    })
  }

  shouldComponentUpdate(nextProps, nextState) {
    return super.shouldComponentUpdate(nextProps, nextState) ||
      (nextProps.state.productionType !== this.props.state.productionType)
  }

  componentWillReceiveProps(nextProps) {
    super.componentWillReceiveProps(nextProps)
    if (nextProps.state.productionType !== this.props.state.productionType) {
      this.loadData(this.props.state.hierarchy.get('entities'), nextProps)
    }
  }
}

export default ReservesDecline



