// react & redux
import React from 'react'

// core classes & libs
import ChartHoverSync from './ChartHoverSync'
import autobind from 'autobind-decorator'
import config from './configs/TypeCurveTotal.config'
import API from '../../lib/api-store'

@autobind class TypeCurveTotal extends ChartHoverSync {
  constructor(props) {
    super(props)

    super.loadConfig(config, {
      'yAxis.title.text': `${props.yAxisTitle}`,
    })
  }

  loadData() {
    let {units, seriesName, normalizedIndex, rawIndex} = this.props

    this.setState({ isLoading: true })

    API.get('?options=utcdates', (err, data) => {
      if (err || !this._isMounted) return false

      // transform data from API
      let { type_curves } = data
      let seriesData = { [seriesName]: [] }

      for (let iMonth = 0; iMonth < 12; iMonth++){
        seriesData[seriesName].push({
          x: iMonth+1,
          y: type_curves[normalizedIndex].data[iMonth].y,
          rawValue: type_curves[rawIndex].data[iMonth].y,
          units
        })
      }

      // inject transformed data into state.chartConfig
      this.setState({
        isLoading: false,
        config: this.state.config.update('series', all => all.map(
          series => series.set('data', seriesData[series.get('name')])
        ))
      })
    })
  }
}

export default TypeCurveTotal



