// react & redux
import React from 'react'

// child components
import Select from 'react-select'
import 'react-select/dist/react-select.css'
import API from '../../../lib/api-store'

export const PortfolioSelect = React.createClass({
  getInitialState () {
    return {
      isLoading: false,
      stateClass: 'noSelection',
      options: []
    }
  },

  generateOptions: function(portfolios) {
    return portfolios.map(p => ({ value: p.id || p.pUID, label: p.name }))
  },

  componentDidMount: function() {
    let portfolioActions = this.props.actions.portfolio
    let portfolioState = this.props.state.portfolio
    let _this = this
    let available = portfolioState.get('available')
    let selected = portfolioState.get('selected')
    let { user } = this.props.state

    let options = this.generateOptions(user.get('portfolios').toJS())
    this.setState({ options })
    if (selected) {
      this.setState({ portfolio: selected, stateClass: undefined })
    }
  },

  selectPortfolio: function(selected) {
    let { portfolio, chartOptions, operatorSelection, wellSelection, hierarchy } = this.props.actions

    // reset chartOptions
    chartOptions.resetDateLimits()

    if (!selected) {
      portfolio.clear()
      this.setState({ portfolio: '', stateClass: 'noSelection' })
    } else {
      this.setState({ portfolio: selected.value, stateClass: undefined })

      API.setPortfolio(selected.value)

      // fire redux state actions
      portfolio.select(selected.value)
      operatorSelection.clearOperators()
      wellSelection.clearWells()
      hierarchy.clear()
    }
  },

  componentWillReceiveProps(newProps) {
    if (newProps.portfolio !== this.state.portfolio) {
      API.setPortfolio(newProps.portfolio)
    }
  },

  render() {
    return (
      <Select value={this.state.portfolio} clearable={true} ref="portfolioSelect" className={this.props.className + ' ' + this.state.stateClass} autosize={true} placeholder="Select a Portfolio..." onChange={this.selectPortfolio} options={this.state.options} />
    )
  }
})

export default PortfolioSelect



