// react & redux
import React from 'react'

// child components
import ProductbarActions from './Actions'

const Productbar = ({ app, user, logoutAction }) => {
  let productBarActions = user
    ? <ProductbarActions user={user} logoutAction={logoutAction} />
    : ''

  return (
  <div className="productbar dark">
    <div className="logo">
      <img height="100%" src="/images/spa-logo.png" title={app.get('description')} />
      <small className="version">{app.get('version')}</small>
    </div>
    { productBarActions }
  </div>
  )
}

export default Productbar



